/*------------------------------------------------------------------
Page title
-------------------------------------------------------------------*/
.page-title {
    position: relative;
    background-color: #f5f5f5;
    h1, h2, h3 {
        margin-bottom: 0;
    }
    span {
        margin-top: 5px;
        display: block;
        color: #929294;
    }
    .container {
        position: relative;
    }
    .breadcrumb {
        position: absolute;
        background-color: transparent;
        width: auto;
        top: 35px !important;
        left: auto; //auto
        right: 25px;
        margin: -10px 0 0 0;
        padding: 0;
        font-size: 12px;
        font-weight: bold;
        z-index: 5;
        &:before {
            display: none;
        }
    }
    .section-breadcrumb {
        position:relative;
        .breadcrumb {
            position: absolute;
            background-color: transparent;
            width: auto;
            top: 0 !important;
            left: 0; //auto
            right: 25px;
            margin: -10px 0 0 0;
            padding: 0;
            font-size: 12px;
            font-weight: bold;
            z-index: 5;
            &:before {
                display: none;
            }
        }
    }
    &.no-bg {
        background: none;
        border-bottom:1px solid #f5f5f5;
    }
    &.page-title-center {
        text-align: center;
        .breadcrumb {
            position: relative;
            top: 0;
            left: 0;
            right: 0;
            margin: 20px 0 0 0;
        }
    }
    &.page-title-right {
        text-align: right;
        .breadcrumb {
            left: 30px;
            right: auto;
        }
    }
}

.page-title[class*='dark-'] .breadcrumb>.active {
    color: #fff;
}

@media screen and (max-width: 768px) {
    .page-title {
        margin-top: 46px;
    }
}

/* ----------------------------------------------------------------
Fixed-background page title
-----------------------------------------------------------------*/
.page-title.fixed-bg {
    background-attachment: fixed;
    background-position: 50% 0;
    background-repeat: no-repeat;
}

@media (min-width: 1480px) {
    .page-title.fixed-bg {
        background-size: contain;
    }
}

@media (max-width: 767px) {
    .page-title,
    .page-title.page-title-right {
        text-align: center;
    }

    .page-title .breadcrumb {
        left: 0 !important;
        right: 0 !important;
        top: 0 !important;
        margin: 10px 0 0 !important;
        position: relative !important;
    }
}